@use 'styles/variables' as *;
@use 'styles/mixins' as *;

.start {
    width: 100%;
    min-height: 100vh;
    background-image: linear-gradient(to bottom right, $white-100 50%, $orange-5 50%);
    overflow: hidden;

    &__container {
        margin-top: 230px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__image {
        display: block;
        max-height: 367px;
        max-width: 50%;
    }

    &__content {
        width: 50%;
    }

    &__title {
        margin-top: 0;
        margin-bottom: 64px;
        font-weight: 600;
        font-size: $basic-title-size;
    }

    &__button {
        width: 296px;
    }

    @include for-smaller-than-tablets {
        &__container {
            margin-top: 100px;
            flex-direction: column;
        }

        &__image {
            max-width: auto;
            max-height: 192px;
            margin-bottom: 24px;
        }

        &__content {
            width: auto;
            text-align: center;
        }

        &__title {
            margin-bottom: 102px;
        }
    }

    @include for-phones {
        &__container {
            margin-bottom: 48px;
        }

        &__content {
            width: 100%;
        }

        &__title {
            font-size: 32px;
        }

        &__button {
            width: 100%;
        }
    }
}
